import { Formik } from "formik";
import React, { useState } from "react";
import { Link, NavLink } from "react-router-dom";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Row,
  Col,
  FormGroup,
  Input,
  Label,
} from "reactstrap";
import ToolTip from "../../../components/ToolTip/ToolTip";

import "./DashNavBar.scss";
import {logOut} from "../../../config/axios-interceptor";

function DashNavbar({ userName = "UserName" }) {
  const [isOpen, setIsOpen] = useState(false);
  const [show, setShow] = useState(false);

  const typeData = [
    {
      typeValue: "Audiokitoblar",
    },
    {
      typeValue: "Ertakterapiya",
    },
    {
      typeValue: "Meditatsiya",
    },
    {
      typeValue: "Video",
    },
    {
      typeValue: "Bepul",
    },
    {
      typeValue: "Jaloliddin Rumiy",
    },
    {
      typeValue: "Tongi mashq",
    },
  ];

  const toggle = () => setIsOpen(!isOpen);
  return (
    <div className="dash-topnavbar">
      <Navbar
        color="light"
        light
        className="bg-white border-bottom"
        fixed="top"
        expand="md"
      >
        <NavbarBrand>
          {" "}
          <Link id="TooltipBrand" to={`/main`}>
            <img
              style={{ width: "100px", height: "auto" }}
              src="http://rovitalk.com/wp-content/uploads/2019/10/logo_big_2x.png"
              alt="RoviTalk – Ertakterapiya, audiokitoblar, psixologik mashqlar va ajdodlar oltin merosi."
            />
          </Link>
          <ToolTip
            orentation="bottom"
            customId="TooltipBrand"
            text="RoviTalk – Ertakterapiya, audiokitoblar, psixologik mashqlar va ajdodlar oltin merosi."
          />
        </NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="mr-auto dash-nav-top" navbar>
            <NavItem>
              <NavLink
                onClick={toggle}
                to="/dashboard/ertakterapiya"
                className="custom-link-dash-nav"
                activeClassName="is-active-dash-nav"
              >
                <span className="icon-Baby icon-dash"></span> Ertakterapiya
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                onClick={toggle}
                to="/dashboard/audiokitoblar"
                className="custom-link-dash-nav"
                activeClassName="is-active-dash-nav"
              >
                <span className="icon-Books icon-dash"></span> Audiokitoblar
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                onClick={toggle}
                to="/dashboard/psihologiya"
                className="custom-link-dash-nav"
                activeClassName="is-active-dash-nav"
              >
                <span className="icon-Angel icon-dash"></span> Psihologiya
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                onClick={toggle}
                to="/dashboard/hikmatlar"
                className="custom-link-dash-nav"
                activeClassName="is-active-dash-nav"
              >
                <span className="icon-Sun icon-dash"></span> Hikmatlar
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                onClick={toggle}
                to="/dashboard/kuzgu"
                className="custom-link-dash-nav"
                activeClassName="is-active-dash-nav"
              >
                <span className="icon-Face-Style icon-dash"></span> Ko'zgu
              </NavLink>
            </NavItem>
          </Nav>

          <Nav className="mr-auto">
            <NavItem>
              <div className="search-dropdown">
                <Formik
                  initialValues={{ data: "", type: [] }}
                  onSubmit={(values, { setSubmitting }) => {
                    setTimeout(() => {
                      alert(JSON.stringify(values, null, 2));
                      setSubmitting(false);
                      setShow(!show);
                    }, 400);
                  }}
                >
                  {({
                    values,
                    errors,
                    touched,
                    handleChange,
                    handleBlur,
                    handleSubmit,
                    isSubmitting,
                    resetForm,
                    /* and other goodies */
                  }) => (
                    <form onSubmit={handleSubmit}>
                      <span
                        onClick={() => {
                          setShow(!show);
                        }}
                        className="search-drop-btn"
                      >
                        <img
                          src="http://rovitalk.com/wp-content/themes/skrn-progression/images/search-icon.png"
                          alt=""
                        />{" "}
                      </span>
                      <input
                        type="text"
                        className="search-input"
                        placeholder="Search for Movies or TV Series"
                        name="data"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.data}
                      />
                      <div
                        className={
                          show
                            ? "search-under-dropdown shadow-lg"
                            : "search-under-dropdown shadow-lg search-under-dropdown-active"
                        }
                      >
                        <Row>
                          <Col sm="6" md="3">
                            <div>
                              <h4 className="text-muted font-weight-bold ">
                                Type:
                              </h4>

                              {typeData.map((item, index) => (
                                <FormGroup check>
                                  <Input
                                    key={index}
                                    type="checkbox"
                                    name="type"
                                    id={index}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={item.typeValue}
                                  />
                                  <Label for={index} check>
                                    {item.typeValue}
                                  </Label>
                                </FormGroup>
                              ))}
                            </div>
                          </Col>
                          <Col sm="6" md="3">
                            <div>
                              <h4 className="text-muted font-weight-bold ">
                                Duration:
                              </h4>
                            </div>
                          </Col>
                        </Row>

                        <div className="w-100 d-flex justify-content-center">
                          <button
                            type="submit"
                            className="btn-green mr-3 px-5"
                            disabled={isSubmitting}
                          >
                            Filter Search
                          </button>
                          <button
                            className="btn-green"
                            onClick={() => {
                              resetForm();
                            }}
                          >
                            Reset
                          </button>
                        </div>
                      </div>{" "}
                    </form>
                  )}
                </Formik>
              </div>
            </NavItem>
          </Nav>

          <Nav className="ml-auto right-user">
            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret className="text-muted">
                <i className="fas fa-user-circle mr-2"></i> {userName}
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem>
                  {" "}
                  <Link to="/dashboard/userprofil">
                    {" "}
                    <span className="icon-User"></span> UserProfil{" "}
                  </Link>{" "}
                </DropdownItem>
                <DropdownItem>
                  <Link to="/dashboard/editprofil">
                    {" "}
                    <span className="icon-Gears"></span> Edit profil{" "}
                  </Link>
                </DropdownItem>

                <DropdownItem>
                  <Link to="/dashboard/tarifolish">
                    {" "}
                    <span className="icon-Credit-Card2"></span> Tarif Olish{" "}
                  </Link>
                </DropdownItem>

                <DropdownItem divider />
                <DropdownItem>
                  <Link to="/main" onClick={() => logOut()} className="log-out-link">
                    {" "}
                    <span className="icon-Power-3"></span> Chiqish{" "}
                  </Link>
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
          </Nav>
        </Collapse>
      </Navbar>
    </div>
  );
}

export default DashNavbar;
