import React, { useState } from "react";

import { Formik } from "formik";

import * as Yup from "yup";
import "./Login.scss";
import { useHistory, useLocation } from "react-router-dom";
import ModalItem from "../../../components/ModalItem/ModalItem";
import ForgotPsw from "../ForgotPsw/ForgotPsw";
// import Axios from "axios";
import { useAuth } from "../../../components/Auth/Auth";
import Registration from "../Registration/Registration";
import { useTranslation } from "react-i18next";
import axios from '../../../config/axios-interceptor';

const LoginSchema = Yup.object().shape({
  phone_number: Yup.string().required("required"),
});

function Login() {
  const [verifi, setVerifi] = useState();
  const { t } = useTranslation();
  let history = useHistory();
  let location = useLocation();
  let auth = useAuth();

  let { from } = location.state || { from: { pathname: "/" } };
  let login = () => {
    auth.signin(() => {
      history.replace(from);
    });
  };

  return (
    <div className="login-page py-5">
      <div className="mb-3">
        <h1 className="text-center w-100 font-weight-bold">
          {t(`home_main_Login.pageTitle`)}
        </h1>
        <p className="text-center w-100 text-muted">
          {t(`home_main_Login.pageTitle2`)}
        </p>
      </div>

      {/* "handleSubmit" will validate your inputs before invoking "onSubmit" */}

      <Formik
        initialValues={{
          phone_number: "",
          verification_code: "",
        }}
        validationSchema={LoginSchema}
        onSubmit={(values, { setSubmitting }) => {
            let phone_number;
            // if (values.phone_number.startsWith('+998')) {
            //     phone_number = values.phone_number.replace('+', '');
            // } else {
            //     phone_number = values.phone_number;
            // }
            if (!verifi) {
                axios.post('login', {
                    phone_number: values.phone_number
                }).then(res => {
                    setSubmitting(false);
                    setVerifi(true);
                }).catch(err => alert(err));
            } else {
                axios.post('verify', {
                    phone_number: values.phone_number,
                    verification_code: values.verification_code
                }).then(res => {
                    const token = res.data.data.token;
                    localStorage.setItem('rovi-authenticationToken', token);
                    login()
                })
                    .catch(err => alert(err))
            }
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          /* and other goodies */
        }) => (
          <form onSubmit={handleSubmit} className="login-form">
            <div className="form-item mb-3">
              <label htmlFor="phone_number" className="text-muted">
                * {t(`home_main_Login.pageTitle3`)}
              </label>
              <input
                id="phone_number"
                type="text"
                name="phone_number"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.phone_number}
                style={{
                  borderColor:
                    errors.phone_number && touched.phone_number
                      ? "red"
                      : "green",
                }}
              />
              <small className="text-danger font-weight-bolder">
                {" "}
                {errors.phone_number &&
                  touched.phone_number &&
                  errors.phone_number}{" "}
              </small>
            </div>

            {verifi && (
              <div className="form-item mb-3">
                <label htmlFor="verification_code" className="text-muted">
                  * {t(`home_main_Login.pageTitle4`)}
                </label>
                <input
                  id="verification_code"
                  type="text"
                  name="verification_code"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.verification_code}
                  style={{
                    borderColor:
                      errors.verification_code && touched.verification_code
                        ? "red"
                        : "green",
                  }}
                />
                <small className="text-danger font-weight-bolder">
                  {" "}
                  {errors.verification_code &&
                    touched.verification_code &&
                    errors.verification_code}{" "}
                </small>
              </div>
            )}

            <div className="mb-3 d-flex justify-content-between w-100">
              <div className="left-box d-flex">
                <input
                  id="saved"
                  type="checkbox"
                  name="saved"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.saved}
                />
                <label htmlFor="saved" className=" text-muted ml-2">
                  {t(`home_main_Login.pageTitle5`)}
                </label>
              </div>
              <div className="right-box">
                <ModalItem
                  buttonLabel={t(`home_main_Login.pageTitle6`)}
                  title={t(`home_main_Login.pageTitle7`)}
                  component={<ForgotPsw />}
                />
              </div>
            </div>

            <button
              type="submit"
              disabled={isSubmitting}
              className="form-btn shadow-sm"
            >
              {t(`home_main_Login.pageTitle`)}
            </button>
            <div className="mt-3">
              <p className="text-muted d-flex">
                <span className="mr-2">{t(`home_main_Login.pageTitle8`)}</span>{" "}
                <ModalItem
                  colorText="green"
                  buttonLabel={t(`home_main_Login.pageTitle9`)}
                  title={t(`home_main_Login.pageTitle9`)}
                  component={<Registration />}
                />
                {/* <Link to="/dashboard/registration"> Ro'yxatdan o'tish </Link>{" "} */}
              </p>
            </div>
          </form>
        )}
      </Formik>
    </div>
  );
}

export default Login;
