import React from "react";
import Slider from "react-slick";

import "./PartnerSlider.scss";
function PartnerSlider() {
  var settings = {
    dots: true,
    infinite: false,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 5,
    initialSlide: 0,
    autoplay: true,
    autoplaySpeed: 2000,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 4,
          infinite: true,
          autoplay: true,
          autoplaySpeed: 2000,
          dots: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          initialSlide: 3,
          autoplay: true,
          autoplaySpeed: 2000,
          dots: true,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          autoplay: true,
          autoplaySpeed: 2000,
          dots: true,
        },
      },
    ],
  };
  return (
    <div className="mt-2 partner-slider">
      <Slider {...settings}>
        <div className="slider-item">
          <img
            src="http://rovitalk.com/wp-content/uploads/2019/10/giza.png"
            alt="partner-one"
          />
        </div>
        <div className="slider-item">
          <img
            src="http://rovitalk.com/wp-content/uploads/2019/10/logo_12.png"
            alt="partner-one"
          />
        </div>
        <div className="slider-item">
          <img
            src="http://rovitalk.com/wp-content/uploads/2019/10/freshlife.png"
            alt="partner-one"
          />
        </div>
        <div className="slider-item">
          <img
            src="http://rovitalk.com/wp-content/uploads/2019/10/logo_13.png"
            alt="partner-one"
          />
        </div>
        <div className="slider-item">
          <img
            src="http://rovitalk.com/wp-content/uploads/2019/10/kayros.png"
            alt="partner-one"
          />
        </div>
      </Slider>
    </div>
  );
}

export default PartnerSlider;
