import React, {useEffect, useState} from "react";
import ReactAudioPlayer from "react-audio-player";

import { Col, Row } from "reactstrap";
import TitleHamlet from "../../../components/TitleHamlet/TitleHamlet";

import "./SingleBookPage.scss";
import axios from "../../../config/axios-interceptor";

function SingleBookPage(props) {
  const [autoplay, setAutoplay] = useState(false);
  const [playlist, setPlaylist] = useState();
  const [data, setData] = useState([]);
  const [indexIcon, setIndexIcon] = useState("");
  const [item, setItem] = useState(null);


  useEffect(() => {
    const { id } = props.match.params;
    if (id) {
      axios.post(`files/${id}`)
          .then(res => {
            setData(res.data.data);
            setItem(res.data.data[0])
          })
          .catch(err => alert(err))
    }
  }, []);

  const audioData = [
    {
      audioTitle: "music one",
      audioPath:
        "https://archive.org/download/calexico2006-12-02..flac16/calexico2006-12-02d1t02.mp3",
    },
    {
      audioTitle: "music two",
      audioPath:
        "https://archive.org/download/ra2007-07-21/ra2007-07-21d1t05_64kb.mp3",
    },
    {
      audioTitle: "music three",
      audioPath:
        "https://archive.org/download/slac2002-02-15/slac2002-02-15d1t07_64kb.mp3",
    },
  ];

  const handleCLick = (audioItem) => {
    setAutoplay(true);
    setItem(audioItem);
    axios.post(`file/${audioItem._id}`, {file_id: audioItem._id})
        .then(res => setPlaylist(res.data.data))
  };

  return (
    <div className="single-book-page">
      <TitleHamlet title="Book" />
      {item ? (
          <Row>
            <Col sm="12" md="3">
              <div>
                <img
                    src={item.image}
                    alt="single-book page"
                />

                <h4 className="text-uppercase font-weight-bold my-2">
                  {item.category_title}
                </h4>
              </div>
            </Col>
            <Col sm="12" md="9">
              <div className="w-100">
                <h4 className="text-muted"> {item.title} </h4>
                <ReactAudioPlayer
                    src={playlist && playlist.link}
                    autoPlay={autoplay}
                    controls
                    onPause={() => {
                      setIndexIcon("");
                    }}
                    preload="metadata"
                    className="w-100 audio-player"
                    controlsList="nodownload"
                />

                <div className="card my-2 shadow-lg rounded p-3">
                  {data.map((audioItem, index) => (
                      <div
                          key={index}
                          onClick={() => handleCLick(audioItem)}
                      >
                        {indexIcon === index ? (
                            <i className={`far fa-pause-circle`}/>
                        ) : (
                            <i className={`far fa-play-circle`}/>
                        )}{" "}
                        {audioItem.title}
                      </div>
                  ))}
                </div>
              </div>
              <div className="w-100">
                <p className="my-3 text-muted">
                  Bolalar juda qiziquvchan bo’lishadi. Atrofdagi voqea hodisalarning
                  sababini, natijasini bilgisi keladi, biror bir holat yuzasidan
                  munosabatingizga qiziqadi, ba’zan shunaqangi ko’p savol
                  berishadiki, qay biriga javob berishni bilmay qolasan kishi.
                  Bunday paytda bolaga to’g’ri ma’lumot berish juda muhim! Ammo biz,
                  kattalar doim ham farzandimizning yonida bo’la olmaymiz. Bolani
                  esa kichkinaligidan mustaqil qaror qabul qilishi, fikr
                  bildirishiga o’rgatib borish lozim.{" "}
                </p>
                <p className="my-3 text-muted">
                  Ertaklar esa bolada ana shu hususiyatlarni rivojlantirishga yordam
                  beradi. Ayniqsa, bugun dunyo psihologlari amalyotida keng
                  qo’llanilayotgan ertakterapiya nafaqat kerakli ma’lumotni
                  yetkazadi, balki davolovchi ta’sirga ega ekanligi bilan ham yuqori
                  bahonalmoqda.
                </p>
              </div>
            </Col>
          </Row>
      ) : (
          <Row>
            <Col sm="12" md="3">
              <div>
                <img
                    src=""
                    alt="single-book page"
                />

                <h4 className="text-uppercase font-weight-bold my-2">
                </h4>
              </div>
            </Col>
            <Col sm="12" md="9">
              <div className="w-100">
                <h4 className="text-muted">Music</h4>
                <ReactAudioPlayer
                    src={playlist}
                    autoPlay={autoplay}
                    controls
                    onPause={() => {
                      setIndexIcon("");
                    }}
                    preload="metadata"
                    className="w-100 audio-player"
                    controlsList="nodownload"
                />

                <div className="card my-2 shadow-lg rounded p-3">
                  {data.map((audioItem, index) => (
                      <div
                          key={index}
                          onClick={() => {
                            handleCLick(
                                audioItem._id,
                                audioItem.title,
                                index
                            );
                          }}
                      >
                        {indexIcon === index ? (
                            <i className={`far fa-pause-circle`}></i>
                        ) : (
                            <i className={`far fa-play-circle`}></i>
                        )}{" "}
                        {audioItem.audioTitle}
                      </div>
                  ))}
                </div>
              </div>
              <div className="w-100">
                <p className="my-3 text-muted">
                  Bolalar juda qiziquvchan bo’lishadi. Atrofdagi voqea hodisalarning
                  sababini, natijasini bilgisi keladi, biror bir holat yuzasidan
                  munosabatingizga qiziqadi, ba’zan shunaqangi ko’p savol
                  berishadiki, qay biriga javob berishni bilmay qolasan kishi.
                  Bunday paytda bolaga to’g’ri ma’lumot berish juda muhim! Ammo biz,
                  kattalar doim ham farzandimizning yonida bo’la olmaymiz. Bolani
                  esa kichkinaligidan mustaqil qaror qabul qilishi, fikr
                  bildirishiga o’rgatib borish lozim.{" "}
                </p>
                <p className="my-3 text-muted">
                  Ertaklar esa bolada ana shu hususiyatlarni rivojlantirishga yordam
                  beradi. Ayniqsa, bugun dunyo psihologlari amalyotida keng
                  qo’llanilayotgan ertakterapiya nafaqat kerakli ma’lumotni
                  yetkazadi, balki davolovchi ta’sirga ega ekanligi bilan ham yuqori
                  bahonalmoqda.
                </p>
              </div>
            </Col>
          </Row>
      )}
    </div>
  );
}

export default SingleBookPage;
